footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #45b6fe;
    height: 5vh; 
    font-size: 1.5vw;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    footer {
        height: 7vh;
        font-size: clamp(1rem, 2vw, 1.4rem);
        padding: 0 5vw;
    }
}

@media (max-width: 480px) {
    footer {
        height: 6vh;
        font-size: clamp(0.8rem, 3vw, 1.2rem);
        padding: 0 5vw;
    }
}
