main {
    padding: 2vh;
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.addEventBtn {
    background-color: #4CAF50;
    color: white;
    padding: 2vh 4vw;
    border: none;
    border-radius: 0.5vh;
    cursor: pointer;
    width: auto;
    font-size: clamp(2vw, 4vw, 3vw);
    min-width: 12vw;
    min-height: 4vh;
    text-align: center;
}

textarea{
    width: 103%;
}

.asdqwe{
    font-size: 2vw;
}

.lol {
    text-align: center;
    margin-top: 2vh;
    font-size: 2vw;
}

.editBtn {
    color: green;
}

.date {
    font-size: 1.8vh;
}

.delBtn {
    color: red;
}

.event-list {
    list-style-type: none;
    padding: 0;
}

.event-item {
    border: 1px solid #ccc;
    border-radius: 1vh;
    margin: 2vh;
    padding: 2vw;
    display: flex;
    align-items: center;
    width: 60vw;
    min-width: 20vw;
}

.event-content {
    display: flex;
    align-items: center;
    cursor: pointer;
    flex-wrap: wrap;
}

.event-content img {
    height: 10vw;
    width: 10vw;
    max-width: 10vw;
    max-height: 10vw;
    border-radius: 1vh;
    margin-right: 2vw;
    object-fit: cover;
}

.event-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.title {
    font-size: clamp(3vw, 5vw, 4vh);
    margin: 0;
}

.desc {
    margin: 2vh 0;
    font-size: clamp(1.5vw, 3vw, 2vh);
}

.event-actions {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.event-actions button {
    background: none;
    border: none;
    cursor: pointer;
    margin-left: 1vw;
    font-size: 2vw;
}

.edit {
    display: flex;
    flex-direction: column;

    button{
        width: 9vw;
        height: 2vw;
        margin-top: 10px;
        font-size: 1vw;
    }
}

.edit p {
    padding: 1vh 0;
    font-size: 1vw;
}

.name{
    font-size: 1.5vw;
}

.description{
    font-size: 1vw;
}

.date{
    font-size: 1vw;
}

.img{
    width: 10vw;
    height: 10vw;
}

.close{
    background-color: red;
    color: white;
    position: relative;
    bottom: -20px;
    left:-25%;
    translate: 50%;
}

@media (max-width: 768px) {
    .event-item {
        flex-direction: column;
        text-align: center;
        width: 50vw;
    }

    .event-content {
        flex-direction: column;
        align-items: center;
    }

    .event-content img {
        margin-bottom: 2vh;
        height: 15vw;
        width: 15vw;
    }

    .addEventBtn {
        width: 35vw;
        text-align: center;
        padding: 3vh 6vw;
        font-size: clamp(2vw, 4vw, 3vw);
    }

    .title {
        font-size: clamp(3vw, 5vw, 4vh);
    }

    .desc {
        font-size: clamp(1.5vw, 3vw, 2vh);
    }
}



@media (max-width: 480px) {
    .title {
        font-size: clamp(4vw, 6vw, 5vh);
    }

    .event-item {
        width: 95vw;
    }

    .modal-content {
        padding: 4vw;
    }

    .modal-content textarea {
        height: 20vh;
    }

    .addEventBtn {
        padding: 3vh 6vw;
        font-size: clamp(2vw, 4vw, 3vw);
        min-width: 20vw;
        min-height: 6vh;
    }

    .desc {
        font-size: clamp(2vw, 3vw, 2.5vh);
    }

    .event-content img {
        height: 20vw;
        width: 20vw;
    }
}